
.product-card {
  box-sizing: border-box;
  max-width: $product-card-width;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &:nth-child(4n) {
    margin-right: 0;
  }

  &__container-img {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
  }

  &__img {
    width: 160px;
    height: 134px;
  }

  &__info {
    margin-bottom: 16px;
    &-title {
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      margin-bottom: 4px;
      color: $color-main-accent;
    }
    &-code {
      font-weight: 400;
      font-size: 10px;
      line-height: 14px;
      color: $color-secondary;
    }
  }

  &__rating-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
  }
  &__count-reviews {
    display: inline-block;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $color-main-accent;
  }
  &__price {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;

    &-title {
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      color: $color-secondary;
    }

    &-value {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      color: $color-main-accent;
    }
  }
  &__button {
    display: flex;
    justify-content: center;

    &-link {
      padding: 11px;
      text-align: center;
      text-decoration: none;
      font-weight: 700;
      font-size: 14px;
      line-height: 16px;
      color: #fff;
      text-transform: uppercase;
      background: $color-accent-blue;
      border-radius: 5px;
      flex-grow: 1;
      border: 1px solid $color-accent-blue;

      &:hover {
        background: #fff;
        border: 1px solid $color-accent-blue;
        color: $color-accent-blue;
      }
    }
  }
}
